import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {environment} from '../../../../../environments/environment';
declare var jQuery: any;
@Component({
  selector: 'poweredby',
  template: `
		<div *ngIf="data.visible == true" >
      <div class="stickyfooter">
        <div class="col1"><a href="javascript:void(0);" class="create-link">Create <span *ngIf="this.windowWidth > 767">Your Own</span> Calculator</a></div>
        <div class="col"><div class=" powered-by">
        <span *ngIf="this.windowWidth > 767">BUILT WITH</span>
        <a target="_blank" href="http://rely.co">
        <img alt="Powered By" src="assets/images/builder/powered-by-logo_black.png">
        </a>
        </div></div>
      </div>
    </div>
	`,
  encapsulation:ViewEncapsulation.None
})

export class PoweredByComponent implements OnInit {
  @Input() data: any;
  url: string;
  windowWidth:any;
  ngOnInit() {
  	this.url = environment.PROTOCOL+ environment.APP_EXTENSION;
    this.windowWidth =  jQuery(window).width();
    console.log(this.windowWidth);
  }
}



